import { Injectable } from "@angular/core";
import {
  IDeleteEmpStatusModel,
  IEmpJobDataModel,
  IEmpJobStatusPositionCardModel,
  IEmpJobStatusPositionModel,
  IEmpJobStatusPositionRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IPageParams } from "app/shared/interfaces/params";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class EmployeeJobInformationService extends AppClientService {

  protected expand =
    `${db.Core_Person_Instance_Org}/${db.core_Person_Instance_Job_Title},
    ${db.Core_Person_Instance_Org}.${db.core_Org},${db.Core_Person_Instance_Org}/${db.core_Person_Instance_Job_Title}.${db.Core_Job_Title},
    ${db.core_Status}, ${db.core_Status_Properties}`;

  constructor() {
    super();
  }


  create(data: IEmpJobStatusPositionRequest) {
    return this.post<IResponse<IEmpJobStatusPositionRequest>>(
      "Personnel/Employees/Create_EmployeeStatusPosition",
      data
    );
  }

  update(data: IEmpJobStatusPositionRequest) {
    return this.post<IResponse<IEmpJobStatusPositionRequest>>(
      "Personnel/Employees/Edit_EmployeeStatusPosition",
      data
    );
  }

  getStatusPositionCard(personInstanceID: number) {
    return this.get<IResponse<IEmpJobStatusPositionCardModel>>(
      "Personnel/Employees/Get_StatusPositionCard",
      {
        params: {
          Person_Instance_ID: personInstanceID,
        },
      }
    );
  }

  getList(employeeInstanceId: number, params: IPageParams) {
    return this.get<IResponseList<IEmpJobDataModel[]>>(
      "Personnel/Employees/GetList_EmployeeStatusPosition",
      {
        params: {
          ...params,
          $filter: `Person_Instance_ID  eq ${employeeInstanceId}`,
          $expand: this.expand,
        },
      }
    );
  }

  getAll(employeeInstanceId: number) {
    return this.get<IResponse<any[]>>(
      "Personnel/Employees/GetAll_EmployeeStatusPosition",
      {
        params: {
          $filter: `Person_Instance_ID  eq ${employeeInstanceId}`,
          $expand: this.expand,
        },
      }
    );
  }

  getAllCurrentJobsByEmployeeId(employeeId: number) { // using in jobTitle org chart
    return this.get<IResponse<IEmpJobStatusPositionModel[]>>(
      "personnel/Employees/GetAll_Current_Job_Emp",
      {
        params: {
          PerinsID: employeeId,
        },
      }
    );
  }

  Delete(params: IDeleteEmpStatusModel) {
    return this.delete<IResponse>("Personnel/Employees/Delete_EmployeeStatusPosition", {
      params: params
    });
  }


}
