import { Component, OnInit, ViewChild } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MatStepper } from "@angular/material/stepper";

@Component({
  selector: "app-policy-group-core",
  templateUrl: "./policy-group-core.component.html",
  styleUrls: ["./policy-group-core.component.scss"]
})
export class PolicyGroupCoreComponent extends AppHelpers implements OnInit {
  @ViewChild("stepper") stepper: MatStepper;
  policyGroupId: number;

  constructor() {
    super();
    this.activatedRoute.paramMap.subscribe((params) => {
      this.policyGroupId = Number(params.get("id")) ?? 0;
    });
  }

  ngOnInit(): void {}

  // ngAfterViewInit() {
  //   this.stepper._getIndicatorType = () => "number";
  // }

  stepChanged() {
    // prevent inputs to be invalid after next & back
    this.stepper.selected.interacted = false;
  }


}
