import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ICustomPolicyResponse, IPolicyGroupModel, IPolicyGroupRequest } from "app/org-management/model/policy-group";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { PolicyGroupService } from "app/org-management/services/policy-group.service";
import {
  IntersectionEmpDialogComponent
} from "app/org-management/components/policy-groups/intersection-emp-dialog/intersection-emp-dialog.component";
import { PolicyGroupGrouping, PolicyGroupGroupingList } from "app/shared/enums/policy-group-grouping";

@Component({
  selector: "app-add-policy-group",
  templateUrl: "./add-policy-group.component.html",
  styleUrls: ["./add-policy-group.component.scss"]
})
export class AddPolicyGroupComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  policyGroupId: number;
  radioList = PolicyGroupGroupingList;

  constructor(private policyGroupService: PolicyGroupService) {
    super();
    this.activatedRoute.paramMap.subscribe((params) => {
      this.policyGroupId = Number(params.get("id")) ?? 0;
    });
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      ar_Name: [null, Validators.required],
      en_Name: [null, Validators.required],
      policyGroup_Type_ID: [null, Validators.required],
      groupingType: [null, Validators.required],
      selectedEmployeesIDs: [null],
      selectedOrgIDs: [null],
      selectedLocationsIDs: [null],
    });


    // validations
    this.formCtrls.groupingType.valueChanges.subscribe(val => {
      // open validation
      if (val == PolicyGroupGrouping.OPEN) {
        this.formCtrls.selectedEmployeesIDs.setValidators([Validators.required]);
      } else {
        this.formCtrls.selectedEmployeesIDs.reset();
        this.formCtrls.selectedEmployeesIDs.removeValidators([Validators.required]);
      }

      // organization validation
      if (val == PolicyGroupGrouping.ORGANIZATIONS) {
        this.formCtrls.selectedOrgIDs.setValidators([Validators.required]);
      } else {
        this.formCtrls.selectedOrgIDs.reset();
        this.formCtrls.selectedOrgIDs.removeValidators([Validators.required]);
      }

      // location validation
      if (val == PolicyGroupGrouping.LOCATION) {
        this.formCtrls.selectedLocationsIDs.setValidators([Validators.required]);
      } else {
        this.formCtrls.selectedLocationsIDs.reset();
        this.formCtrls.selectedLocationsIDs.removeValidators([Validators.required]);
      }

      // job title validation
      // if (val == PolicyGroupGrouping.JOB_TITLE) {
      //   this.formCtrls.selectedLocationsIDs.setValidators([Validators.required]);
      // } else {
      //   this.formCtrls.selectedLocationsIDs.reset();
      //   this.formCtrls.selectedLocationsIDs.removeValidators([Validators.required]);
      // }
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IPolicyGroupRequest = {
        core_PolicyGroup: {
          id: this.form.value.id,
          ar_Name: this.form.value.ar_Name,
          en_Name: this.form.value.en_Name,
          policyGroup_Type_ID: this.form.value.policyGroup_Type_ID,
          groupingType: this.form.value.groupingType,
        },
        selectedEmployeesIDs: this.form.value.selectedEmployeesIDs,
        selectedOrgIDs: this.form.value.selectedOrgIDs,
        selectedLocationsIDs: this.form.value.selectedLocationsIDs,
      };

      model.core_PolicyGroup.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IPolicyGroupRequest) {
    this.showLoading();
    this.policyGroupService
      .create(model)
      .subscribe((res: ICustomPolicyResponse) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          if (res?.intersectionEmployees?.length > 0) {
            this.intersectionEmpDialog(res);
          }
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.clearForm();
        } else {
          this.errorList(res.message.messages);
        }

        this.hideLoading();
      });
  }

  update(model: IPolicyGroupRequest) {
    this.showLoading();
    this.policyGroupService.edit(model).subscribe((res: IResponse<IPolicyGroupModel>) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.clearForm();
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
      } else {
        this.errorList(res.message.messages);
      }
      this.hideLoading();
    });
  }

  intersectionEmpDialog(model: ICustomPolicyResponse) {
    this.dialogsService
      .addEditDialog(IntersectionEmpDialogComponent, model)
      .afterClosed()
      .subscribe(() => {
        // this.getAll();
      });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

}
