import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { orgKpiDisplay } from "app/org-management/mapper/org-kpi";
import {
  IOrgKpiModel,
  IOrgKpiRequest,
} from "app/org-management/model/org-kpis";
import { OrgKpisService } from "app/org-management/services/org-structure/org-kpis.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-org-kpis",
  templateUrl: "./org-kpis.component.html",
  styleUrls: ["./org-kpis.component.scss"],
})
export class OrgKpisComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = ["Ser", "kpi", "actions"];
  isLoaded: boolean = true;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IOrgKpiModel>;

  constructor(public orgKpisService: OrgKpisService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      OrgTypeId: [null, [Validators.required]],
      OrgId: [null, [Validators.required]],
      KPI_Id: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    // if (this.checkNoBusinessOrCompany()) {
    // }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getList(page: number, pageSize?: number) {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);

    let orgId = this.formCtrls.OrgId.value;
    if (orgId > 0) {
      this.isLoaded = false;
      this.page.pageIndex = page;
      if (pageSize) this.page.pageSize = pageSize;
      let params: IPageParams = {
        PageNumber: page,
        pageSize: this.page.pageSize,
      };
      this.orgKpisService
        .getList(params, orgId)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponseList<IOrgKpiModel[]>) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.dataSource = new MatTableDataSource(orgKpiDisplay(res.data));
            this.isLoaded = true;
            this.page.length = res.meta.totalItemCount;
          } else this.errorList(res.message.messages);
        });
    }
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IOrgKpiRequest = {
        OrgId: this.form.value.OrgId,
        KPI_Id: this.form.value.KPI_Id,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IOrgKpiRequest) {
    this.showLoading();
    this.orgKpisService
      .create(model)
      .subscribe((res: IResponse<IOrgKpiModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.lastPage);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  onDelete(kpi: IOrgKpiModel): void {
    let msg = kpi.kpi;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(kpi);
    });
  }

  delete(kpi: IOrgKpiModel) {
    this.showLoading();
    this.orgKpisService.Delete(kpi.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.pageSize);
        } else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.formCtrls.KPI_Id.reset();
    this.submitted = false;
  }
}
