import { msgTranslate } from "app/shared/resources/helpers-functions";
import { IOrgKpiModel } from "../model/org-kpis";

export function orgKpiDisplay(data: IOrgKpiModel[]): IOrgKpiModel[] {
  return data.map((kpi) => {
    return <IOrgKpiModel>{
      ...kpi,
      kpi: msgTranslate(kpi.core_KPI?.ar_Name, kpi.core_KPI?.en_Name),
    };
  });
}
