import { Injectable } from "@angular/core";
import { orgKpiDisplay } from "app/org-management/mapper/org-kpi";
import {
  IOrgKpiModel,
  IOrgKpiRequest,
} from "app/org-management/model/org-kpis";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class OrgKpisService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IOrgKpiRequest) {
    return this.post<IResponse<IOrgKpiModel>>(
      "OrgStructure/Create_KpiOrg",
      data
    );
  }

  getList(params: IPageParams, orgId: number) {
    return this.get<IResponseList<IOrgKpiModel[]>>(
      "OrgStructure/GetList_KpiOrg",
      {
        params: {
          ...params,
          $expand: db.core_KPI,
          $filter: `orgId eq ${orgId}`,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IOrgKpiModel[]>>(
      "OrgStructure/GetAll_KpiOrg"
    ).pipe(
      map((res) => {
        res.data = orgKpiDisplay(res.data);
        return res;
      })
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("OrgStructure/Delete_KpiOrg", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    // this.getAll();
  }
}
