import { Component, Inject, OnInit } from "@angular/core";
import { ICustomPolicyResponse, IIntersectionEmpRequest } from "app/org-management/model/policy-group";
import { MAT_DIALOG_DATA } from "@angular/material/dialog";
import { PolicyGroupService } from "app/org-management/services/policy-group.service";
import { FormGroup } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-intersection-emp-dialog",
  templateUrl: "./intersection-emp-dialog.component.html",
  styleUrls: ["./intersection-emp-dialog.component.scss"]
})
export class IntersectionEmpDialogComponent implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  appHelper: AppHelpers = new AppHelpers();
  selectedEmployeeIds: number[];

  constructor(@Inject(MAT_DIALOG_DATA) public model: ICustomPolicyResponse,
              private policyGroupService: PolicyGroupService) {
    console.log(model);
  }

  ngOnInit(): void {
    this.form = this.appHelper.fb.group({
      policyStartDate: [""]
    });
  }


  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const body: IIntersectionEmpRequest[] = [];
      this.selectedEmployeeIds.forEach(empId => {
        let item: IIntersectionEmpRequest = {
          employeeId: empId,
          policyGroupTypeId: this.model.data.policyGroup_Type_ID,
          policyStartDate: this.form.controls["policyStartDate"].value
        };
        body.push(item);
      });

      this.appHelper.showLoading();
      this.policyGroupService
        .saveIntersectionEmployees(body)
        .subscribe((res: ICustomPolicyResponse) => {
          if (res?.message?.messageType == MessageTypes.Success) {
            if (res?.intersectionEmployees?.length > 0) {
            }
            this.appHelper.addSuccessfullyMsg(res?.message?.messageTypeName);
          } else {
            this.appHelper.errorList(res.message.messages);
          }

          this.appHelper.hideLoading();
        });

    } // else this.toastr.error("General.formNotValid");
  }

  onSelectEmployee(selected: boolean, id: number) {
    if (selected) {
      this.selectedEmployeeIds.push(id);
    } else {
      let i = this.selectedEmployeeIds.indexOf(id);
      this.selectedEmployeeIds.splice(i, 1);
    }
  }

}
